.cv-show {
	& > .cv-show__main {
		padding: 20px 40px;
	
		& > .main {
			&__title {
				margin-top: 50px;
				text-align: center;
			}
			
			&__content {
				@include flex(column);
				align-items: flex-start;
				border: 3px solid $color-grey;
				margin-top: 40px;
				padding: 40px;
				
				& > .content {
					&__title {
						position: relative;
						border-bottom: 4px solid $color-grey;
						width: 100%;
					
						& > h4 {
							text-align: left !important;
							font-weight: bold;
						}
						
						& > span {
							position: absolute;
							color: $color-grey_1;
							font-size: 1rem;
							top: 40px;
						}
					}
					
					&__items {
						& > .items {
							&__item {
								margin-top: 30px;
								
								& > span {
									margin-right: 10px;
								}
							}
						}
					}
				}
			}
			
			&__button-wrapper {
				@include flex();
				margin-top: 20px;
				
				& > .main__button {
					text-align: center;
					padding: 20px;
					margin: -20px;
					margin-top: 20px;
					margin: 20px;
					
					& > button {
						margin: 0 10px;
					}
				}
			}
		}
	}
}